import React, { useState, useRef, useEffect, useCallback } from 'react';
import { 
  Box, 
  IconButton, 
  Typography, 
  Slider, 
  Tooltip, 
  Menu,
  MenuItem,
  ListItemText,
  ListItemIcon
} from '@mui/material';
import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import PauseIcon from '@mui/icons-material/Pause';
import SkipNextIcon from '@mui/icons-material/SkipNext';
import SkipPreviousIcon from '@mui/icons-material/SkipPrevious';
import VolumeUpIcon from '@mui/icons-material/VolumeUp';
import VolumeOffIcon from '@mui/icons-material/VolumeOff';
import MusicNoteIcon from '@mui/icons-material/MusicNote';
import QueueMusicIcon from '@mui/icons-material/QueueMusic';

const MusicPlayer = () => {
  const [isPlaying, setIsPlaying] = useState(false);
  const [currentTrack, setCurrentTrack] = useState(0);
  const [volume, setVolume] = useState(0.7);
  const [isMuted, setIsMuted] = useState(false);
  const [progress, setProgress] = useState(0);
  const [duration, setDuration] = useState(0);
  const [tracks, setTracks] = useState([]);
  const [anchorEl, setAnchorEl] = useState(null);
  const audioRef = useRef(null);
  const progressInterval = useRef(null);

  // Load tracks from the music folder - reduced number of tracks
  useEffect(() => {
    // This would typically be an API call to get the list of tracks
    // For now, we'll hardcode a few placeholder tracks
    const availableTracks = [
      { id: 1, title: 'Cyberpunk Theme', file: '/music/cyberpunk-theme.mp3' },
      { id: 2, title: 'Night City', file: '/music/night-city.mp3' }
    ];
    
    setTracks(availableTracks);
  }, []);

  // Memoized play/pause handler to reduce re-renders
  const handlePlayPause = useCallback(() => {
    setIsPlaying(prev => !prev);
  }, []);

  useEffect(() => {
    if (audioRef.current) {
      if (isPlaying) {
        audioRef.current.play().catch(error => {
          console.error("Error playing audio:", error);
          setIsPlaying(false);
        });
        
        // Update progress less frequently (every 2 seconds instead of every second)
        progressInterval.current = setInterval(() => {
          if (audioRef.current) {
            setProgress(audioRef.current.currentTime);
          }
        }, 2000);
      } else {
        audioRef.current.pause();
        if (progressInterval.current) {
          clearInterval(progressInterval.current);
        }
      }
    }
    
    return () => {
      if (progressInterval.current) {
        clearInterval(progressInterval.current);
      }
    };
  }, [isPlaying]);

  // Update audio source when track changes
  useEffect(() => {
    if (audioRef.current && tracks.length > 0) {
      const currentTrackData = tracks[currentTrack];
      if (currentTrackData) {
        audioRef.current.src = currentTrackData.file;
        audioRef.current.load();
        
        // Wait for metadata to load to get duration
        audioRef.current.onloadedmetadata = () => {
          setDuration(audioRef.current.duration);
          setProgress(0);
          
          if (isPlaying) {
            audioRef.current.play().catch(error => {
              console.error("Error playing audio after track change:", error);
              setIsPlaying(false);
            });
          }
        };
      }
    }
  }, [currentTrack, tracks, isPlaying]);

  // Update volume when it changes - memoized to reduce re-renders
  const handleVolumeChange = useCallback((event, newValue) => {
    setVolume(newValue);
    if (audioRef.current) {
      audioRef.current.volume = newValue;
    }
    // If volume is set above 0, unmute
    if (newValue > 0 && isMuted) {
      setIsMuted(false);
    }
  }, [isMuted]);

  // Toggle mute - memoized to reduce re-renders
  const handleMuteToggle = useCallback(() => {
    setIsMuted(prev => {
      const newMuted = !prev;
      if (audioRef.current) {
        audioRef.current.muted = newMuted;
      }
      return newMuted;
    });
  }, []);

  // Handle progress change - memoized to reduce re-renders
  const handleProgressChange = useCallback((event, newValue) => {
    setProgress(newValue);
    if (audioRef.current) {
      audioRef.current.currentTime = newValue;
    }
  }, []);

  // Skip to next track - memoized to reduce re-renders
  const handleNext = useCallback(() => {
    setCurrentTrack(prev => (prev + 1) % tracks.length);
  }, [tracks.length]);

  // Skip to previous track - memoized to reduce re-renders
  const handlePrevious = useCallback(() => {
    setCurrentTrack(prev => (prev - 1 + tracks.length) % tracks.length);
  }, [tracks.length]);

  // Format time for display (mm:ss)
  const formatTime = useCallback((time) => {
    if (isNaN(time)) return '00:00';
    const minutes = Math.floor(time / 60);
    const seconds = Math.floor(time % 60);
    return `${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`;
  }, []);

  // Handle track menu open
  const handleMenuOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  // Handle track menu close
  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  // Select a track from the menu
  const handleTrackSelect = (index) => {
    setCurrentTrack(index);
    handleMenuClose();
  };

  // Get current track title
  const getCurrentTrackTitle = () => {
    if (tracks.length === 0) return 'No tracks available';
    return tracks[currentTrack]?.title || 'Unknown';
  };

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        p: 1,
        backgroundColor: 'rgba(10, 10, 30, 0.7)',
        borderRadius: '8px',
        color: '#fff',
        width: '300px',
        border: 'none',
      }}
    >
      <Box sx={{ display: 'flex', alignItems: 'center', width: '100%', mb: 1 }}>
        <IconButton 
          size="small" 
          onClick={handlePrevious}
          sx={{ color: '#00fff0' }}
        >
          <SkipPreviousIcon />
        </IconButton>
        
        <IconButton 
          size="small" 
          onClick={handlePlayPause}
          sx={{ 
            color: '#00fff0',
            mx: 1,
            '&:hover': {
              backgroundColor: 'rgba(0, 255, 240, 0.1)',
            }
          }}
        >
          {isPlaying ? <PauseIcon /> : <PlayArrowIcon />}
        </IconButton>
        
        <IconButton 
          size="small" 
          onClick={handleNext}
          sx={{ color: '#00fff0' }}
        >
          <SkipNextIcon />
        </IconButton>
        
        <Box sx={{ flex: 1, ml: 1 }}>
          <Tooltip title={getCurrentTrackTitle()}>
            <Typography 
              variant="caption" 
              noWrap
              sx={{ 
                color: '#00fff0',
                textShadow: '0 0 5px rgba(0, 255, 240, 0.7)',
                cursor: 'default',
                maxWidth: '150px',
                display: 'block'
              }}
            >
              {getCurrentTrackTitle()}
            </Typography>
          </Tooltip>
          
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <Typography variant="caption" sx={{ color: '#aaa', mr: 0.5, fontSize: '0.6rem' }}>
              {formatTime(progress)}
            </Typography>
            
            <Slider
              size="small"
              value={progress}
              max={duration || 100}
              onChange={handleProgressChange}
              sx={{ 
                color: '#00fff0',
                height: 4,
                '& .MuiSlider-thumb': {
                  width: 8,
                  height: 8,
                  '&:hover, &.Mui-focusVisible': {
                    boxShadow: '0 0 0 8px rgba(0, 255, 240, 0.16)',
                  },
                },
                '& .MuiSlider-rail': {
                  opacity: 0.5,
                },
                flex: 1,
                mx: 0.5
              }}
            />
            
            <Typography variant="caption" sx={{ color: '#aaa', fontSize: '0.6rem' }}>
              {formatTime(duration)}
            </Typography>
          </Box>
        </Box>
        
        <Box sx={{ display: 'flex', alignItems: 'center', ml: 1 }}>
          <IconButton 
            size="small" 
            onClick={handleMuteToggle}
            sx={{ color: '#00fff0' }}
          >
            {isMuted ? <VolumeOffIcon fontSize="small" /> : <VolumeUpIcon fontSize="small" />}
          </IconButton>
          
          <IconButton
            size="small"
            onClick={handleMenuOpen}
            sx={{ color: '#00fff0' }}
          >
            <QueueMusicIcon fontSize="small" />
          </IconButton>
        </Box>
      </Box>
      
      <Menu
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={handleMenuClose}
        PaperProps={{
          sx: {
            backgroundColor: 'rgba(10, 10, 30, 0.9)',
            border: '1px solid rgba(0, 255, 240, 0.3)',
            boxShadow: '0 0 10px rgba(0, 255, 240, 0.3)',
            color: 'white',
            maxHeight: '300px'
          }
        }}
      >
        <Typography 
          variant="subtitle2" 
          sx={{ 
            px: 2, 
            py: 1, 
            color: '#00fff0',
            borderBottom: '1px solid rgba(0, 255, 240, 0.3)'
          }}
        >
          Playlist
        </Typography>
        
        {tracks.map((track, index) => (
          <MenuItem 
            key={track.id} 
            onClick={() => handleTrackSelect(index)}
            selected={index === currentTrack}
            sx={{
              '&.Mui-selected': {
                backgroundColor: 'rgba(0, 255, 240, 0.2)',
              },
              '&:hover': {
                backgroundColor: 'rgba(0, 255, 240, 0.1)',
              }
            }}
          >
            <ListItemIcon sx={{ color: '#00fff0', minWidth: '30px' }}>
              <MusicNoteIcon fontSize="small" />
            </ListItemIcon>
            <ListItemText 
              primary={track.title} 
              primaryTypographyProps={{ 
                variant: 'body2',
                sx: { color: index === currentTrack ? '#00fff0' : 'white' }
              }}
            />
          </MenuItem>
        ))}
      </Menu>
      
      <audio ref={audioRef} preload="metadata" />
    </Box>
  );
};

export default React.memo(MusicPlayer);
